soc205.controller('Act7Ctrl', function ($scope) {
	$scope.inputs = [];
	$scope.$root.isNextEnabled = true; // Activamos el siguiente link

	$scope.items = [];
	$scope.texts = [
		"Cul medio de transporte utilizan para transportarse a su lugar de trabajo?",
		"Es un medio de transporte areo, terrestre o acutico?",
		"Qu normas debe cumplir para hacer un buen uso de este medio?"
	];

	// llenamos el array
	$scope.texts.forEach(function(text){
		$scope.items.push({
			text: text,
			input: ''
		});
	});

	// antes de ir a la siguiente actividad, verificamos
	$scope.$root.beforeGoNext = function () {
		var error = false,
			str = '';

		// Recorremos los elementos, buscando que cumplan con las condiciones
		// Al menos una mayscula, una minscula y 4 letras
		$scope.items.forEach(function(item){
			str = item.input.toLowerCase();
			if( !( str.match(/([bcdfghjklmnpqrstvwxyz])/) && str.match(/([aeiou])/) && str.length >= 4 ) ) error = true;
		});

		if(error){
			// fracado
			$scope.failure = true;
			return false;
		} else {
			// xito
			$scope.success = true;
			return true;
		}
	};
	
});
