esp210.controller('Act5Ctrl', function ($scope) {
   $scope.$root.isNextEnabled = true; // Activamos el siguiente link

  $scope.input = '';
  $scope.selectedItem = false; // elemento seleccionado

  $scope.items = [
    {
      src: "tristeza",
      alt: "Fotografa donde se muestra un nio llorando",
      title: "Evans, J. (2006). Sad boy 1 [Fotografa]. Obtenido de: http://www.freeimages.com/photo/517525"
    },
    {
      src: "aburrida",
      alt: "Fotografa donde se muestra una joven con cara de aburricin",
      title: "Aburrido [Fotografa]. (2012). Obtenido de: http://pixabay.com/es/libro-aburrido-universidad-15584/"
    },
    {
      src: "madre",
      alt: "Fotografa donde se muestra una mujer cargando una beb, ambas estn sonriendo",
      title: "Madre [Fotografa]. (2014). Obtenido de: http://pixabay.com/es/madre-beb%C3%A9-feliz-familia-ni%C3%B1o-kid-316213/ "
    }
  ];

  $scope.$root.selectItem = function (item) {

        $scope.selectedItem = item; // seleccionamos el objeto
  };

  // antes de ir a la siguiente actividad, verificamos
  $scope.$root.beforeGoNext = function () {
    var error = false,
      str = '';

    str = $scope.input.toLowerCase();
    if( !( str.match(/([bcdfghjklmnpqrstvwxyz])/) && str.match(/([aeiou])/) && str.length >= 10 ) ) error = true;

      if(error){
        // fracaso
        $scope.failure = true;
        return true;
      } else {
        // xito
        $scope.success = true;
        return true;
      }
    };
});

