nat209.controller('Act5Ctrl', function ($scope) {
   $scope.$root.isNextEnabled = true; // Activamos el siguiente link

  $scope.input = '';
  $scope.selectedItem = false; // elemento seleccionado

  $scope.items = [
    {
      src: "vaso",
      alt: "Vaso plstico desechable",
      text: "Vaso de plstico",
    },
    {
      src: "vasoplastico",
      alt: "Vaso de vidrio",
      text: "Vaso de vidrio",
      title: "Gdula, A. (2008). Glass of wter [Fotografa]. Obtenido de: http://www.freeimages.com/browse.phtml?f=download&id=964550"
    },
    {
      src: "pocillo",
      alt: "Pocillo de cermica con una oreja a un lado",
      text: "Pocillo",
      title: "White coffee cup empty [Fotografa]. (2013). Obtenido en: http://www.freeimages.com/photo/1418279"
    }
  ];

  $scope.$root.selectItem = function (item) {

        $scope.selectedItem = item; // seleccionamos el objeto
  };

  // antes de ir a la siguiente actividad, verificamos
  $scope.$root.beforeGoNext = function () {
    var error = false,
      str = '';

    str = $scope.input.toLowerCase();
    if( !( str.match(/([bcdfghjklmnpqrstvwxyz])/) && str.match(/([aeiou])/) && str.length >= 10 ) ) error = true;

      if(error){
        // fracaso
        $scope.failure = true;
        return true;
      } else {
        // xito
        $scope.success = true;
        return true;
      }
    };
});

